import gsap from 'gsap';
import Draggable from 'gsap/Draggable';
// import InertiaPlugin from 'gsap/InertiaPlugin';

import Swiper, { Scrollbar } from 'swiper';
import 'swiper/css';
import 'swiper/css/scrollbar';

import 'ScssComponents/scrollbar.scss';

import { Fancybox } from '@fancyapps/ui';
import '@fancyapps/ui/dist/fancybox/fancybox.css';
import 'ScssComponents/gallery.scss';

import { BREAKPOINTS } from 'utils/constants';
import { exist, onWindowResize } from '../utils';
import { buildSwiper, removeSwiper } from './buildSwiper';

const gallery = () => {
	Fancybox.bind('[data-fancybox]', {});
	gsap.registerPlugin(Draggable);

	const classNames = {
		slider: '.js-slider-gallery-wrapper',
		wrapper: '.js-slider-gallery',
		scrollbar: '.js-slider-gallery-scrollbar',
		scrollbarDesk: '.js-slider-gallery-scrollbar-desk',
	};

	const $sliderWrappers = document.querySelectorAll(classNames.wrapper);
	const $scrollBarDesk = document.querySelector(classNames.scrollbarDesk);

	let draggable;
	let widthWrapper;
	let widthScrolledBlock;
	let percent;

	if (!exist($sliderWrappers)) return;

	$sliderWrappers.forEach(($wrapper) => {
		const $slider = $wrapper.querySelector(classNames.slider);
		if (!$slider) return;

		let isMobile;
		let latestSwiper;

		const $scrollbar = $wrapper.querySelector(classNames.scrollbar);

		const initSlider = () => {
			buildSwiper($slider);

			latestSwiper = new Swiper($slider, {
				modules: [Scrollbar],
				speed: 800,
				scrollbar: {
					el: $scrollbar,
					draggable: true,
				},
				breakpoints: {
					320: {
						slidesPerView: 1.55,
					},
					768: {
						slidesPerView: 3,
					},
				},
			});
		};

		const updateInfo = () => {
			widthWrapper = Math.floor(gsap.getProperty('.gallery__list_drag', 'width') || 0);
			widthScrolledBlock = Math.floor(gsap.getProperty('.gallery__list', 'width') || 0) - widthWrapper;
			percent = 0;
		};

		const checkOverflow = () => {
			if (widthScrolledBlock > 0) {
				// eslint-disable-next-line
				if ($scrollBarDesk) $scrollBarDesk.parentElement.style.display = 'block';
				return true;
			} else {
				if ($scrollBarDesk) $scrollBarDesk.parentElement.style.display = 'none';
				return false;
			}
		};

		const initDragSlider = () => {
			checkOverflow();
			updateInfo();

			draggable = Draggable.create($slider, {
				zIndexBoost: false,
				type: 'x',
				edgeResistance: 1,
				bounds: { minX: 0, maxX: -widthScrolledBlock },

				onDrag: () => {
					percent = (Math.abs(Math.round(gsap.getProperty($slider, 'x'))) / widthScrolledBlock) * 100;
					// eslint-disable-next-line
					$scrollBarDesk.style.transform = `translateX(${percent}%)`;
				},
			});

			if (!checkOverflow()) draggable.disable();
		};

		const toggleSlider = () => {
			isMobile = window.innerWidth < BREAKPOINTS.mediaPoint1;
			if (isMobile && !$slider.classList.contains('swiper-initialized')) {
				initSlider();
			} else if (!isMobile && $slider.classList.contains('swiper-initialized')) {
				latestSwiper.destroy(true, true);
				removeSwiper($slider);
			}

			if (draggable) draggable[0].kill();
			initDragSlider();
		};

		onWindowResize(toggleSlider);
	});
};

export default gallery;
