import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Swiper, { Pagination } from 'swiper';
import 'swiper/css';
import { buildSwiper, removeSwiper } from './buildSwiper';

const relatedArtcielsSlider = ({ rootSelector, breakpoint }) => {
	const sliderWrapper = document.querySelectorAll(rootSelector);

	const slider = document.querySelectorAll('.js-mobile-slider');

	let sliderArr = [];
	let isInit = false;

	const init = () => {
		if (!isInit) {
			slider.forEach((item) => {
				buildSwiper(item);

				let sliderEl = new Swiper(item, {
					modules: [Pagination],
					slidesPerView: 'auto',
					on: {
						init: () => {
							isInit = true;
						},
					},
				});
				sliderArr.push(sliderEl);
			});
		}
	};

	const destroy = () => {
		if (isInit) {
			slider.forEach((item) => {
				removeSwiper(item);
				sliderArr.forEach((item) => {
					item.destroy();
				});
				isInit = false;
			});
		}
	};

	const matchMedia = gsap.matchMedia();

	matchMedia.add([`(min-width: ${breakpoint}px)`], () => {
		destroy();
	});
	matchMedia.add([`(max-width: ${breakpoint - 1}px)`], () => {
		init();
	});
};

export default relatedArtcielsSlider;
